#footer {
  width: 100%;
  height: 100px;
  display: flex;
  position: relative;
  z-index: 1;
  background: #f4f4f4;
  user-select: none;
  .left  {
    width: 200px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    h1 {
      width:100%;
      height: 40px;
      a {
        font-size: 24px;
        font-weight: 600;
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        span {
          &:nth-child(1) {  // span:nth-child(1) => &:nth-child(1) => &는 this를 의미함
            color: #5ac;
            filter: grayscale(1);
            opacity: 0.5;
          }
          &:nth-child(2) {
            margin-left: 5px;
            color: #c5a;
            filter: grayscale(1);
            opacity: 0.5;
          }
        }
      }
    }
  }

  .center {
    width: 800px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .container {
      text-align: center;
      line-height: 170%;
      address {
        font-size: 13px;
        width:100%;color: #666;
      }
      p {
        font-size: 11px;
        width:100%;
        color: #999;
      }
    }
  }

  .right {
    width: 200px;height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .container {
      a {
        margin: 0 5px;
        img {
          width:24px;
        }
      }
    }
  }
}