/* 팝업창 */
/* 
화면의 맨위 맨좌측
전체너비 높이 
*/
.popup {
  z-index: 4;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, 0.5);

  /* 팝업창 가운데 */
  display: flex;
  justify-content: center;
  align-items: center;
  /* display: none; */
  &.on {
    display: flex;
  }
  .container {
    width: 500px;
    background: #fff;
    border: 1px solid #ccc;
    padding: 20px;
    .title {width:100%;
      h1 {
        font-size: 20px;
        font-weight: 600;
        color:#222;
        border-bottom: 1px solid #ddd;
        padding-bottom: 20px;
      }
    }
    .content {
      padding-top: 20px;
      ul {width:100%;
        li {width:100%;line-height: 180%;}
      }
    }
    .button-box {padding-top: 50px;text-align: right;
      button {
        width: 60px;
        height: 35px;
        border: 1px solid #ccc;
        background: #fff;
        color: #333;
        cursor: pointer;   
        border-radius: 3px; 
        font-weight: 500;
        &:hover {
          background: #555;
          color: #fff;
        }
      }
    }
  }
}